/* Import Bulma */
@import "~bulma/css/bulma.css";

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center; /* Center items horizontally */
  justify-content: center; /* Center items vertically */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: inherit; /* Inherit color from parent, making links look like normal text */
  text-decoration: none; /* Remove underline */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* User defined styles */

html,
body {
  margin: 0;
  padding: 0;
  background-color: #282c34; /* Match your header background */
  color: white; /* Ensure text is white */
  display: flex;
  justify-content: center; /* Center body content horizontally */
  /* Removed align-items for vertical centering */
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center items horizontally */
  margin: 20px;
  width: 100%; /* Ensure full width */
  max-width: 800px; /* Optional: set a maximum width for better layout */
}

.title {
  margin-bottom: 10px;
  display: flex; /* Use flex to align the title and arrow */
  align-items: center; /* Center vertically */
}

.arrow {
  margin-left: 10px; /* Space between title and arrow */
  font-size: 24px; /* Adjust size of the arrow */
  transition: transform 0.3s ease; /* Smooth transition for rotation */
}

/* Style for arrow rotation */
.title.open .arrow {
  transform: rotate(180deg); /* Point down when open */
}

.table {
  width: 60%;
  min-width: 200px; /* Ensures a minimum width of 200px */
  border-collapse: collapse;
  transition: max-height 0.3s ease, opacity 0.3s ease; /* Transition for max-height and opacity */
  overflow: hidden; /* Hide overflow for the sliding effect */
  max-height: 0; /* Default to collapsed */
  opacity: 0; /* Default to hidden */
  display: block; /* Use block to take up space */
}

.table.visible {
  max-height: 500px; /* Set to a value greater than expected max height of the content */
  opacity: 1; /* Fully opaque when visible */
  display: table; /* Display as a table when visible */
}

.table.hidden {
  max-height: 0; /* Collapses to 0 height */
  opacity: 0; /* Makes it transparent */
  display: none; /* Prevents it from taking up space */
}

.cell {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center; /* Ensure text is centered in each cell */
  /* Add these properties to vertically center the text */
  display: flex; /* Use flexbox to align items */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
}

.component-container {
  margin-top: 20px; /* Ensure space between the toolbox and the component */
}

/* Astromon Card */

.card-container {
  display: flex;
  gap: 10px;
}

.astromon-card {
  width: 150px;
  padding: 10px;
  background-color: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.astromon-image {
  width: 100px;
  height: 100px;
  object-fit: contain;
  opacity: 0.5; /* To apply the fade effect */
  margin-bottom: 10px;
}

.astromon-stats {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.stat-item {
  font-size: 14px;
  margin: 5px 0;
}

/* Tool Cards */
.tool-page {
  text-align: center;
  margin-top: 20px;
}

.card-area {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.tool-card {
  width: 200px; /* Adjusted width for more content */
  height: 320px; /* Adjusted height for more content */
  border: 1px solid #ccc;
  border-radius: 10px;
  display: flex;
  flex-direction: column; /* Changed to column for vertical alignment */
  align-items: center; /* Center content horizontally */
  justify-content: flex-start; /* Start content from the top */
  margin-right: 10px;
  background-color: #282c34; /* Background color for the card */
  position: relative; /* For positioning background image */
  padding: 10px; /* Add padding for inner spacing */
  color: #fff; /* White text color for better contrast */
}

/* Pseudo-element for the background image */
.tool-card::before {
  content: ""; /* Required to make the pseudo-element display */
  position: absolute; /* Position it absolutely within the card */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("../public/icons/Aesir3EvoD_large.png"); /* Add your background icon */
  background-size: cover; /* Ensure background covers the card */
  background-position: center; /* Center the background image */
  opacity: 0.4; /* Set the opacity for the background */
  z-index: -1; /* Place it behind the content */
}

.tool-card.blank {
  cursor: pointer;
}

.plus-icon {
  font-size: 24px;
  color: #888;
}

.selected-name {
  display: flex;
  align-items: center; /* Aligns icon and text vertically */
  margin: 10px 0; /* Space around the selected name */
}

.selected-name img {
  margin-right: 10px; /* Space between icon and name */
}

.evolution-icons {
  margin: 10px 0; /* Space around evolution icons */
}

.evolution-icons img {
  cursor: pointer;
  width: 40px; /* Set a uniform width for evolution icons */
  height: 40px; /* Set a uniform height for evolution icons */
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Two columns for stats */
  gap: 5px; /* Space between stats */
  margin: 10px 0; /* Space around stats grid */
}

.skill-boxes {
  display: flex;
  justify-content: space-between; /* Space between skill boxes */
  margin-top: 10px; /* Space above skill boxes */
}

.skill-boxes div {
  width: 40px; /* Width of skill boxes */
  height: 40px; /* Height of skill boxes */
  border: 1px solid #fff; /* White border for skill boxes */
  border-radius: 5px; /* Rounded corners for skill boxes */
  text-align: center; /* Center text in boxes */
  line-height: 40px; /* Center text vertically */
  cursor: pointer; /* Pointer cursor on hover */
}

input[type="text"] {
  width: 100%; /* Full width for search box */
  padding: 10px; /* Padding for better appearance */
  margin-bottom: 10px; /* Space below the search box */
  border: 1px solid #ccc; /* Border for search box */
  border-radius: 5px; /* Rounded corners */
  background-color: #fff; /* White background for search box */
  color: #000; /* Black text color for search box */
}

ul {
  list-style-type: none; /* Remove default list styles */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margin */
  width: 100%; /* Full width for result list */
}

li {
  padding: 5px; /* Padding for list items */
  border-bottom: 1px solid #ccc; /* Divider between items */
  cursor: pointer; /* Pointer cursor on hover */
}

li:hover {
  background-color: rgba(255, 255, 255, 0.1); /* Highlight effect on hover */
}
