/* builder.css */

.tool-page {
  padding: 20px;
}

.card-area {
  display: flex; /* Use flexbox for horizontal layout */
  flex-wrap: wrap; /* Allow wrapping to the next line */
  gap: 10px; /* Space between cards */
}

.card-wrapper {
  flex: 0 0 auto; /* Prevent cards from growing or shrinking */
}

/* Style for the card options popup */
.card-options {
  margin-top: 20px; /* Space above the card options */
  padding: 10px; /* Inner padding for the card options */
  background-color: #f0f0f0; /* Background color for visibility */
  border: 1px solid #ccc; /* Border around the options */
  border-radius: 5px; /* Rounded corners */
}

.card-options button {
  display: block; /* Display buttons as block elements */
  margin: 5px 0; /* Space between buttons */
  padding: 10px; /* Inner padding for buttons */
  background-color: #007bff; /* Button background color */
  color: white; /* Button text color */
  border: none; /* Remove default border */
  border-radius: 5px; /* Rounded corners for buttons */
  cursor: pointer; /* Pointer cursor on hover */
  transition: background-color 0.3s; /* Smooth background transition */
}

.card-options button:hover {
  background-color: #0056b3; /* Darker blue on hover */
}
